<template>
  <div class="addednews-wrapper">
    <div>
      <p class="header-wrap">{{yxTitle}}</p>
    </div>
    <div class="control-wrap">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" enctype="multipart/form-data">
        <el-form-item label="" prop="titlename">
          <el-input class="newstitleInput" v-model="ruleForm.titlename" style="width: 30%;"></el-input>
          <span class="newstitleTip">12</span>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input class="newstitleInput" v-model="ruleForm.title"></el-input>
          <span class="newstitleTip">40</span>
        </el-form-item>
        <el-form-item prop="content" style="width: 80%;">
          <quill-editor class="quill-editor" ref="myQuillEditor"
            v-model="ruleForm.content" :options="editorOption" @change="onEditorChange($event)" @focus="onEditorFocus($event)"></quill-editor>
          <!-- <div class="limit"> <span>{{nowLengthNum}}</span>  <span>{{SurplusLengthNum}}</span> </div> -->
          <div class="limit" style="border:none"></div>
          <!-- input -->
          <el-upload class="upload-demo" :action="qnLocation" :before-upload='beforeUpload' :data="uploadData" :on-success='upScuccess'
            ref="upload" style="display:none">
            <el-button size="small" type="primary" id="imgInput" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text=","></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button @click="backnews"></el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor, Quill } from 'vue-quill-editor';
import { fetchAddNews, modifylist, modifynews, updataUrl } from 'oa/api/statis/news'
import { fetchAppInfo, editAPP, editSiteAPP, fetchSiteAppInfo } from 'oa/api/process/ruxue'
import { mapGetters } from 'vuex'
const SUCCESS_OK = '200'
  export default {
    components: {
      quillEditor,
      //  VueCropper
    },
    data() {
      let checkImg = (rule, value, callback) => {
        if(value === '') {
          return callback (new Error(''))
        } else {
          callback();
        }
      }
      return {
        title: '',
        yxTitle: '',
        yinxin: '',
        ruxueSiteState: '',
        appItem: '',
        eaaLogicId: '',
        uploadType : '',    // 
        uploadData : {},
        STATICDOMAIN: '',
        fullscreenLoading : false,
        updataurl: updataUrl,
        ruleForm: {
          cropImg: '',
          title: '',
          content: '', // 
          titlename: ''
        },
        dialogTableVisible: false,
        nowLengthNum: '0',
        SurplusLengthNum: '2000',
        rules: {
          title: [
            { required: true, message: '', trigger: 'blur' },
            { max: 40, message: '40', trigger: 'blur' }
          ],
          titlename: [
            { required: true, message: '', trigger: 'blur' },
            { max: 40, message: '12', trigger: 'blur' }
          ],
          cropImg: [
            {required: true, validator: checkImg, trigger: 'blur'}
          ],
          content: [
            { required: true, message: '', trigger: 'blur' },
            // { max: 2000, message: '2000', trigger: 'blur' }
          ]
        },
        defaultSrc: './static/img/nodata1.png',
        fileList: [],
        imgSrc: '',
        qnLocation: updataUrl,
        dialogVisible: false,
        editorOption: {},
        imageList: [
          {width : 600, height: 600, url: './static/img/nodata1.png'}
          // { width: 600, height: 600, url: 'http://ocm0knkb1.bkt.clouddn.com/1-1.jpg' }
        ]
      }
    },
    methods: {
      // ICON
      imgHandler(state) {
        this.addRange = this.$refs.myQuillEditor.quill.getSelection()
        if (state) {
          let fileInput = document.getElementById('imgInput')
          fileInput.click() // 
        }
        this.uploadType = 'image'
      },
      // ICON
      videoHandler(state) {
        this.addRange = this.$refs.myQuillEditor.quill.getSelection()
        if (state) {
          let fileInput = document.getElementById('imgInput')
          fileInput.click() // 
        }
        this.uploadType = 'video'
      },
       // 
      onEditorFocus(editor) {
        console.info(editor)
        editor.enable(true)   // 
        if (this.SurplusLengthNum === 2000){
          editor.enable(false)   // 
        }
      },
      // 
      beforeUpload(file) {
        return this.qnUpload(file)
      },
      //    
      upScuccess(e, file, fileList) {
        this.fullscreenLoading = false
        let vm = this
        let url = ''
        if (this.uploadType === 'image') {    // URL
          url = e.data.url
        } else if (this.uploadType === 'video') {
          // console.log(e)
          url = e.data.url
        }
        if (url != null && url.length > 0) {  // URL
          let value = url
          vm.addRange = vm.$refs.myQuillEditor.quill.getSelection()
          value = value.indexOf('http') !== -1 ? value : 'http:' + value
          vm.$refs.myQuillEditor.quill.insertEmbed(vm.addRange !== null ? vm.addRange.index : 0, vm.uploadType, value, Quill.sources.USER)   //  insertEmbed URL
        } else {
          this.MessageError(`${vm.uploadType}`)
        }
        this.$refs['upload'].clearFiles()    // input
      },
      // token
      qnUpload(file) {
        this.fullscreenLoading = true
        const suffix = file.name.split('.')
        const ext = suffix.splice(suffix.length - 1, 1)[0]
        console.log(this.uploadType)
        if (this.uploadType === 'image') {  // 
          this.uploadData = {
            key: `image/${suffix.join('.')}_${new Date().getTime()}.${ext}`,
          }
        } else if (this.uploadType === 'video') {  // 
          this.uploadData = {
            key: `video/${suffix.join('.')}_${new Date().getTime()}.${ext}`,
          }
        }
      },
      // 
      onEditorChange({ quill, html, text }) {
        this.ruleForm.content = html;
        let textLength = text.length - 1
        this.nowLengthNum = textLength
        let _totalNum = 2000
        let num = _totalNum - Number(textLength)
        if (num > 0) {
          this.SurplusLengthNum =  num
        } else {
          this.SurplusLengthNum = 0
        }
        // if (textLength >= _totalNum) {
        //   this.MessageError('2000')
        //   quill.enable(false)
        //   return
        // }
      },
      //  
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.ruxueSiteState === 1) {
              this._fetchAddNews()
            } else if (this.ruxueSiteState === 3) {
              this._editSiteApp()
            }
          } else {
            this.$message.error('');
            console.log('');
            return false;
          }
        });
      },
      // 
      _fetchAddNews(){
        let loading = this.loading()
        editAPP(this.eaaLogicId, this.ruleForm.titlename, '2', '1', this.bulletinLogicId, this.ruleForm.title, this.ruleForm.content ).then(res => {
          loading.close()
          console.info(res)
          if(res.data.state === SUCCESS_OK){
            // console.log(this.ruleForm.content);
            this.MessageSuccess(res.data.message)
            setTimeout(() => {
              this.$router.push({path: '/yingxin/ruxue/'})
            }, 1500);
          } else {
            this.MessageError(res.data.message)
          }
        }).catch(_ => {
          loading.close()
          this.MessageError('')
        })
      },
      // 
      _editSiteApp(){
        let loading = this.loading()
        editSiteAPP(this.eaaLogicId, this.ruleForm.titlename, '2', '1', this.bulletinLogicId, this.ruleForm.title, this.ruleForm.content).then(res => {
          loading.close()
          console.info(res)
          if(res.data.state === SUCCESS_OK){
            // console.log(this.ruleForm.content);
            this.MessageSuccess(res.data.message)
            setTimeout(() => {
              this.$router.push({path: '/yingxin/ruxue/'})
            }, 1500);
          } else {
            this.MessageError(res.data.message)
          }
        }).catch(_ => {
          loading.close()
          this.MessageError('')
        })
      },
      //  
      _fetchAppInfo(){
        if(this.eaaLogicId === ''){
          // this.imageList[0].url = this.defaultSrc
          return
        } else {
          fetchAppInfo(this.eaaLogicId).then(res => {
            res = res.data
            if(res.state === SUCCESS_OK){
            console.info(res.data)
              this.ruleForm.cropImg = res.data.bulletinUrl
              this.ruleForm.titlename = res.data.enrollAndApplication.enrollName
              this.ruleForm.title = res.data.list[0].bulletinTitle
              this.ruleForm.content = res.data.list[0].bulletinText
              this.bulletinLogicId = res.data.list[0].bulletinLogicId
              // this.imageList[0].url = res.data.bulletinUrl  // 
            } else {
              this.MessageError(res.message)
            }
          })
        }
      },
      //  
      _fetchSiteAppInfo(){
        if(this.eaaLogicId === ''){
          // this.imageList[0].url = this.defaultSrc
          return
        } else {
          fetchSiteAppInfo(this.eaaLogicId).then(res => {
            res = res.data
            if(res.state === SUCCESS_OK){
            console.info(res.data)
              this.ruleForm.cropImg = res.data.bulletinUrl
              this.ruleForm.titlename = res.data.enrollAndApplication.enrollName
              this.ruleForm.title = res.data.list[0].bulletinTitle
              this.ruleForm.content = res.data.list[0].bulletinText
              this.bulletinLogicId = res.data.list[0].bulletinLogicId
              // this.imageList[0].url = res.data.bulletinUrl  // 
            } else {
              this.MessageError(res.message)
            }
          })
        }
      },
      // 
      modify(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this._modifynew()
          } else {
            this.$message.error('');
            console.log('');
            return false;
          }
        })
      },
      // 
      judgeView () {
        this.ruxueSiteState = this.$store.getters.ruxueSiteState
        this.appItem = this.$store.getters.appItem
        this.eaaLogicId = this.$store.getters.appItem.eaaId
        if (this.ruxueSiteState === '') {
          this.MessageError('')
          this.$router.push({path: '/yingxin/ruxue/'})
        } else if (this.ruxueSiteState === 1) {
          // 
          this._fetchAppInfo()
        } else if (this.ruxueSiteState === 3) {
          // 
          this._fetchSiteAppInfo()
        }
      },
      // 
      backnews() {
        this.$router.push({path: '/yingxin/ruxue/'})
      },
      beforeAvatarUpload(file) {
        console.info(file)
        // const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        // if (!isJPG) {
        //   this.$message.error(' JPG !');
        // }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        // return isJPG && isLt2M;
        return isLt2M;
      },
    },
    mounted () {
      // ICON  getModule 
      this.$refs.myQuillEditor.quill.getModule('toolbar').addHandler('image', this.imgHandler)
      this.$refs.myQuillEditor.quill.getModule('toolbar').addHandler('video', this.videoHandler)  // ICON
      this.judgeView()
    }
  }
</script>
<style lang="stylus" scoped>
.addednews-wrapper
  width 98%
  margin 0 1%
.header-wrap
  width 100%
  height 49px
  line-height 50px
  font-size 14px
  border-bottom 1px solid #2184C5
  // display flex
  // justify-content space-between
  p
    color #555
    font-weight bold
    display inline-block
    font-family MicrosoftYaHei-Bold
  .crumbs
    display inline-block
    position relative
    top 2px
    left 11px
    font-size 10px
    border-left 2px solid #a7a7a7
    padding-left 10px
.control-wrap
  margin-top 25px
  .newstitleInput
    width 50%
  .newstitleTip
    padding-left 12px
    font-size 12px
    color #999
  .quill-editor
    // width 60%
    // height 400px
    // margin 10px 0 0 -80px
    padding-bottom 58px
  .crop-demo
    display flex
    align-items flex-end
    .listImg
      overflow: hidden;
      width 100px
      height 100px
      margin 10px 0
      .pre-img
        width 100px
        height 100px
    .crop-demo-btn
      position relative
      top -40px
      width 68px
      height 24px
      line-height 24px
      padding 0 20px
      margin-left 30px
      // background-color #409eff
      color #999
      font-size 12px
      border-radius 12px
      border 1px solid #999
      box-sizing border-box
      &:hover
        background-color #409eff
        color: #fff
        border none
      .crop-input
        position absolute
        width 100px
        height 40px
        left 0
        top 0
        opacity 0
        cursor pointer
    .crop-demo-tip
      position relative
      left -70px
      color: #999999
      font-size 12px
.avatar-uploader
  width 100px
  height 100px
  border 1px dashed #d9d9d9
  border-radius 6px
  cursor pointer
  position relative
  overflow hidden
  display inline-block
  &:hover
    border-color #409EFF
.avatar-uploader-icon 
  font-size 28px
  color #8c939d
  width 100px
  height 100px
  line-height 100px
  text-align center
.avatar 
  width 100px
  height 100px
  display block
.crop-demo-btn
  width 68px
  height 24px
  line-height 24px
  padding 0 20px
  color #999
  font-size 12px
  border-radius 12px
  border 1px solid #999
  box-sizing border-box
  cursor pointer
  &:hover
    background-color #409eff
    color: #fff
    border none
.crop-demo-tip
  color: #999999
  font-size 12px
.setImageBtn
  display inline-block
  margin-left 20px
.quill-editor
  height 745px
  .ql-container.ql-snow
    height 680px
    .ql-editor
      img 
        max-width 90%
      .ql-video
        max-width 70%
.limit
  height: 30px;
  border: 1px solid #ccc;
  line-height: 30px;
  text-align: right;
  span
    color: #ee2a7b;

</style>



